import "../styles/editor.scss";
import { __ } from "@wordpress/i18n";
import { withInstanceId, compose } from "@wordpress/compose";
import { withSelect } from "@wordpress/data";
import {
	BlockControls,
	useBlockProps,
	AlignmentToolbar,
} from "@wordpress/block-editor";
import { Placeholder, Spinner } from "@wordpress/components";
import { decodeEntities } from "@wordpress/html-entities";
import { getSettings, dateI18n } from "@wordpress/date";
import { useEffect } from "@wordpress/element";
import InspectorPanel from "./inspector";
import { prefix } from "../../../constants";
import {
	isUndefined,
	pickBy,
	wordTrim,
	capitalize,
	getTypoStyle,
	findIndex,
	getResponsiveRangeVal,
	getDimensionStyle,
} from "../../../helpers";

const Edit = (props) => {
	const {
		setAttributes,
		instanceId,
		attributes: {
			enableContent,
			excerptLength = 20,
			contentTypo,
			contentColor,
			enableTitle,
			titleTypo,
			titleColor,
			titleHoverColor,
			imageSize,
			metaColor,
			enableDate,
			enableCategory,
			enableAuthor,
			metaTypo,
			alignment,
			height,
			smallHeight,
			padding,
			overlayColor,
			categories,
		},
		latestPosts,
		clientId,
	} = props;

	const hasPosts = Array.isArray(latestPosts) && latestPosts.length;

	if (!hasPosts) {
		return (
			<>
				<InspectorPanel {...props} />
				<Placeholder icon="admin-post" label={__("News 1", "rise-blocks")}>
					{!Array.isArray(latestPosts) ? (
						<Spinner />
					) : (
						__("No posts found.", "rise-blocks")
					)}
				</Placeholder>
			</>
		);
	}

	useEffect(() => {
		setAttributes({
			block_id: `${prefix}-news-1-block-instance-${instanceId}-${clientId}`,
		});
	}, []);

	const getExcerpt = (post) => {
		if (enableContent) {
			const style = {
				...getTypoStyle(contentTypo),
				color: contentColor,
			};
			return (
				<p style={style}>
					{wordTrim(post["rise-blocks_excerpt"], excerptLength)}
				</p>
			);
		}
	};

	const title = ({ title }, i) => {
		if (enableTitle) {
			const style = {
				...getTypoStyle(titleTypo),
				color: titleColor,
				":hover": {
					color: titleHoverColor,
				},
			};

			return (
				<h2 className={`${prefix}-news-1-post-title`}>
					<a key={i} onClick={(e) => e.preventDefault()} href="#" style={style}>
						{decodeEntities(title.rendered.trim()) ||
							__("(Untitled)", "rise-blocks")}
					</a>
				</h2>
			);
		}
	};

	const getImgUrl = ({ _embedded }) => {
		let img = "";
		if (
			!isUndefined(_embedded) &&
			!isUndefined(_embedded["wp:featuredmedia"]) &&
			!isUndefined(
				_embedded["wp:featuredmedia"][0].source_url,
			)
		) {
			var size = _embedded["wp:featuredmedia"][0].media_details.sizes;

			if (size[imageSize]) {
				img = size[imageSize] ? _embedded["wp:featuredmedia"][0].source_url : "";
			} else {
				img = _embedded["wp:featuredmedia"][0].source_url ;
			}
		}

		return img;
	};

	const meta = (post) => {
		const { _embedded, date_gmt } = post;
		const style = {
			...getTypoStyle(metaTypo),
			color: metaColor,
		};

		const dateFormat = getSettings().formats.date;

		if (enableAuthor || enableDate || enableCategory) {
			return (
				<div className={`${prefix}-news-1-meta-wrapper`}>
					{enableAuthor && _embedded.author[0].avatar_urls && (
						<div className={`${prefix}-news-1-author meta-content`}>
							<a
								onClick={(e) => e.preventDefault()}
								style={style}
								href="#"
								title={capitalize(_embedded.author[0].name)}
							>
								<i className="fa fa-user"></i>
							</a>
						</div>
					)}

					{enableDate && (
						<div className={`${prefix}-news-1-post-date meta-content`}>
							<a onClick={(e) => e.preventDefault()} style={style} href="#">
								<span>
									<i className="fa fa-calendar"></i>{" "}
									{dateI18n(dateFormat, date_gmt)}
								</span>
							</a>
						</div>
					)}
				</div>
			);
		}
	};

	const blockProps = useBlockProps({
		className: `${prefix}-news-1-wrapper ${prefix}-align-${alignment}`,
	});

	return (
		<>
			<InspectorPanel {...props} />
			<BlockControls>
				<AlignmentToolbar
					value={alignment}
					onChange={(alignment) => setAttributes({ alignment })}
				/>
			</BlockControls>
			<section {...blockProps}>
				{latestPosts.map((post, i) => {
					let odd = i % 3 === 0 ? "big" : "small";
					let style = {
						height:
							i % 3 === 0
								? getResponsiveRangeVal(height)
								: getResponsiveRangeVal(smallHeight),
						backgroundImage: "url(" + getImgUrl(post) + ")",
						...getDimensionStyle("padding", padding),
					};

					let rb_cat = post["rise-blocks_categories"];
					let cat = "";

					if (
						isUndefined(categories) &&
						Array.isArray(rb_cat) &&
						rb_cat.length
					) {
						cat = rb_cat[0].cat_name;
					} else {
						let j = findIndex(rb_cat, { term_id: parseInt(categories) });
						cat = rb_cat[j].cat_name;
					}

					let catStyle = {
						...getTypoStyle(metaTypo),
						color: metaColor,
					};

					return (
						<div
							key={i}
							style={style}
							className={`${prefix}-news-1-card-wrapper col-${odd}`}
						>
							<div
								className={`${prefix}-news-1-overlay`}
								style={{ backgroundColor: overlayColor }}
							></div>
							<div className={`${prefix}-news-1-card`}>
								<div className={`${prefix}-news-1-body`}>
									<div className={`${prefix}-news-1-body-inner`}>
										{enableCategory && (
											<div className={`${prefix}-news-1-post-cat meta-content`}>
												<a
													onClick={(e) => e.preventDefault()}
													style={catStyle}
													href="#"
												>
													<span>{cat}</span>
												</a>
											</div>
										)}

										{title(post, i)}

										{meta(post)}
										<div className={`${prefix}-news-1-post-content`}>
											{getExcerpt(post)}
										</div>
									</div>
								</div>
							</div>
						</div>
					);
				})}
			</section>
		</>
	);
};

const applyWithSelect = withSelect((select, { attributes }) => {
	const { postsToShow, order, orderBy, categories } = attributes;
	const { getEntityRecords } = select("core");

	const latestPostsQuery = pickBy(
		{
			categories,
			order,
			orderby: orderBy,
			per_page: postsToShow,
			_embed: true,
		},
		(value) => !isUndefined(value),
	);

	return {
		latestPosts: getEntityRecords("postType", "post", latestPostsQuery),
	};
});

export default compose(applyWithSelect, withInstanceId)(Edit);
