import { __ } from "@wordpress/i18n";
import {
	InspectorControls,
	InspectorAdvancedControls,
} from "@wordpress/block-editor";
import { useEffect, useState } from "@wordpress/element";
import {
	QueryControls,
	ColorIndicator,
	PanelBody,
} from "@wordpress/components";

import { addQueryArgs } from "@wordpress/url";
import apiFetch from "@wordpress/api-fetch";
import { createHooks } from "@wordpress/hooks";

import {
	SwitchControl,
	PanelColor,
	ExcerptLengthControl,
	ColorControl,
} from "../../../custom-control";

import { prefix } from "../../../constants";

import Typography from "../../../custom-control/typography";
import {
	ImageSizeControl,
	BackgroundOverlayControl,
} from "../../../custom-control";
import ResponsiveRangeWrapperControl from "../../../custom-control/responsive-range-wrapper";

import Dimension from "../../../custom-control/dimension";

import Tab from "../../../components/tab";
import Exporter from "../../../components/exporter";
import metadata from "../block.json";

const InspectorPanel = (props) => {

	const [categoriesList, setCategoriesList] = useState([]);
	const { applyFilters } = createHooks();

	const {
		attributes: {
			order,
			orderBy,
			categories,
			postsToShow,
			titleTypo,
			contentTypo,
			metaTypo,
			enableTitle,
			enableContent,
			enableCategory,
			enableAuthor,
			enableDate,
			imageSize,
			titleColor,
			titleHoverColor,
			contentColor,
			metaColor,
			overlayColor,
			excerptLength,
			height,
			smallHeight,
			padding,
		},
		setAttributes,
		clientId,
	} = props;

	useEffect(() => {
		const onGet = (categoriesList) => {
			setCategoriesList(categoriesList);
		};

		const onError = () => {
			setCategoriesList([]);
		};

		const CATEGORIES_LIST_QUERY = {
			per_page: -1,
		};

		const params = {
			path: addQueryArgs(`/wp/v2/categories`, CATEGORIES_LIST_QUERY),
		};

		if (categoriesList.length === 0) {
			apiFetch(params).then(onGet).catch(onError);
		}
	}, [categoriesList]);

	const titleColorData = {
		titleColor: {
			value: titleColor,
			title: __("Normal", "rise-blocks"),
		},
		titleHoverColor: {
			value: titleHoverColor,
			title: __("Hover", "rise-blocks"),
		},
	};

	const titleColorSetting = Object.keys(titleColorData).map((key) => {
		const value = titleColorData[key].value;
		return {
			title: () => (
				<div>
					{titleColorData[key].title}
					<ColorIndicator colorValue={value} />
				</div>
			),
			content: () => (
				<ColorControl
					value={value}
					hideIndicator={true}
					onChange={(c) => setAttributes({ [key]: c })}
				/>
			),
		};
	});

	return (
		<>
			<InspectorAdvancedControls>
				<Exporter clientId={clientId} />
			</InspectorAdvancedControls>

			<InspectorControls>
				<PanelBody title={__("General", "rise-blocks")} initialOpen={true}>
					<QueryControls
						order={order}
						orderBy={orderBy}
						categoriesList={categoriesList}
						selectedCategoryId={categories}
						numberOfItems={postsToShow}
						onCategoryChange={(value) =>
							setAttributes({ categories: "" !== value ? value : undefined })
						}
						maxItems={applyFilters("riseBlocksBlogLimit", 100)}
						onNumberOfItemsChange={(value) =>
							setAttributes({ postsToShow: value })
						}
						onOrderChange={(value) => setAttributes({ order: value })}
						onOrderByChange={(value) => setAttributes({ orderBy: value })}
					/>
				</PanelBody>

				<PanelBody title={__("Content", "rise-blocks")} initialOpen={false}>
					<SwitchControl
						label={__("Enable Title", "rise-blocks")}
						checked={enableTitle}
						onChange={(enableTitle) => {
							setAttributes({ enableTitle });
						}}
					/>

					<SwitchControl
						label={__("Enable Excerpt", "rise-blocks")}
						checked={enableContent}
						onChange={(enableContent) => {
							setAttributes({ enableContent });
						}}
					/>

					{enableContent && (
						<ExcerptLengthControl
							value={excerptLength}
							onChange={(excerptLength) => setAttributes({ excerptLength })}
						/>
					)}

					<SwitchControl
						label={__("Enable Category", "rise-blocks")}
						checked={enableCategory}
						onChange={(enableCategory) => {
							setAttributes({ enableCategory });
						}}
					/>
					<SwitchControl
						label={__("Enable Author", "rise-blocks")}
						checked={enableAuthor}
						onChange={(enableAuthor) => {
							setAttributes({ enableAuthor });
						}}
					/>
					<SwitchControl
						label={__("Enable Date", "rise-blocks")}
						checked={enableDate}
						onChange={(enableDate) => {
							setAttributes({ enableDate });
						}}
					/>

					<ResponsiveRangeWrapperControl
						label={__("Height", "rise-blocks")}
						beforeIcon=""
						value={height}
						onChange={(height) => setAttributes({ height })}
					/>

					<ResponsiveRangeWrapperControl
						label={__("Small Height", "rise-blocks")}
						beforeIcon=""
						value={smallHeight}
						onChange={(smallHeight) => setAttributes({ smallHeight })}
					/>
				</PanelBody>

				<PanelBody title={__("Image", "rise-blocks")} initialOpen={false}>
					<ImageSizeControl
						value={imageSize}
						onChange={(imageSize) => setAttributes({ imageSize })}
					/>
				</PanelBody>

				<PanelBody title={__("Typography", "rise-blocks")} initialOpen={false}>
					<Typography
						label={__("Title", "rise-block")}
						value={titleTypo}
						defaultValue={metadata.attributes.titleTypo.default}
						onChange={(titleTypo) => setAttributes({ titleTypo })}
					/>
					<hr />
					<Typography
						label={__("Content", "rise-block")}
						value={contentTypo}
						defaultValue={metadata.attributes.contentTypo.default}
						onChange={(contentTypo) => setAttributes({ contentTypo })}
					/>
					<hr />
					<Typography
						label={__("Meta", "rise-block")}
						value={metaTypo}
						defaultValue={metadata.attributes.metaTypo.default}
						onChange={(metaTypo) => setAttributes({ metaTypo })}
					/>
				</PanelBody>

				<PanelBody title={__("Spacing", "rise-blocks")} initialOpen={false}>
					<Dimension
						label={__("Padding", "rise-block")}
						value={padding}
						defaultValue={metadata.attributes.padding.default}
						onChange={(padding) => setAttributes({ padding })}
					/>
				</PanelBody>

				<PanelColor
					colorSettings={[
						{
							value: contentColor,
							onChange: (contentColor) => setAttributes({ contentColor }),
							label: __("Content", "rise-blocks"),
						},
						{
							value: metaColor,
							onChange: (metaColor) => setAttributes({ metaColor }),
							label: __("Meta", "rise-blocks"),
						},
					]}
				>
					<Tab
						className={`${prefix}-tabbed-color-control`}
						label={__("Title", "rise-blocks")}
						data={titleColorSetting}
					/>

					<BackgroundOverlayControl
						value={overlayColor}
						onChange={(overlayColor) => setAttributes({ overlayColor })}
					/>
				</PanelColor>
			</InspectorControls>
		</>
	);
};

export default InspectorPanel;
