import { __ } from "@wordpress/i18n";
import { Fragment, useState } from "@wordpress/element";
import { Button } from "@wordpress/components";
import ResponsiveRangeControl from "../components/responsive-range-control";

import { defaultResponsiveViews, prefix } from "../constants";

const ResponsiveRangeWrapperControl = (props) => {
	const [activeView, setActiveView] = useState("desktop");

	const changeActiveView = (newActiveView) => {
		setActiveView(newActiveView);
	};

	const onChange = (label, fontSize) => {
		props.onChange(fontSize);
	};

	const reset = () => {
		const { onChange, defaultValue } = props;
		defaultValue && onChange(defaultValue);
	};

	const {
		value,
		defaultValue,
		step = "1",
		beforeIcon = "editor-textcolor",
	} = props;

	return (
		<>
			<ResponsiveRangeControl
				changeActiveView={changeActiveView}
				activeView={activeView}
				name="fontSize"
				step={step}
				responsiveViews={defaultResponsiveViews}
				value={value}
				onChange={onChange}
				label={props.label}
				beforeIcon={beforeIcon}
			/>
			{defaultValue && (
				<div className={`${prefix}-btn-reset-wrapper`}>
					<Button onClick={reset} className={`is-button is-default is-small`}>
						{__("Reset", "rise-blocks")}
					</Button>
				</div>
			)}
		</>
	);
};

export default ResponsiveRangeWrapperControl;
